.alert {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    border-radius: $radius;
    box-shadow: 0 2px 9px 0 rgba(0,0,0,0.02);
    color: $white;

    &__text {

        &--has-icon {
            display: flex;
            align-items: flex-start;

            .icon {
                flex-shrink: 0;
                width: 20px;
                height: 20px;
                fill: $white;
                margin-right: 10px;
            }
        }
    }

    &--success {
        background: $success--grad;
    }

    &--error {
        background: $error--grad;
    }

    &--centered {
        justify-content: center;
    }

    &--pinned {
        border-radius: 0 0 $radius $radius;
        position: fixed;
        z-index: 10;
        width: 100%;
        top: 0;
        transition: top 1s;
    }
}



